@import url(https://fonts.googleapis.com/css2?family=Archivo:wght@400;700&family=Poppins&display=swap);
#page-landing{width:100vw;height:100vh;overflow:auto;display:flex;justify-content:center;background-color:var(--color-primary);color:var(--color-text-in-primary)}#page-landing #page-landing-content{margin:auto}#page-landing .logo-container{text-align:center;margin:3.2rem 0}#page-landing .logo-container img{height:10rem}#page-landing .logo-container h2{font-weight:500;font-size:2.4rem;line-height:4.6rem;margin-top:0.8rem}#page-landing .hero-image{width:100%}#page-landing .buttons-container{display:flex;justify-content:center;margin:3.2rem 0}#page-landing .buttons-container a{display:flex;align-items:center;justify-content:center;width:30rem;height:10.4rem;border-radius:0.8rem;font:700 2rem Archivo;text-decoration:none;color:var(--color-button-text);transition:background-color 200ms}#page-landing .buttons-container a:not(:last-child){margin-right:1.6rem}#page-landing .buttons-container a img{width:4rem;margin-right:1rem}#page-landing .buttons-container a.study{background-color:var(--color-primary-lighter)}#page-landing .buttons-container a.study:hover{background-color:var(--color-primary-light)}#page-landing .buttons-container a.teach{background-color:var(--color-secondary)}#page-landing .buttons-container a.teach:hover{background-color:var(--color-secondary-dark)}#page-landing .total-connections{display:flex;align-items:center;justify-content:center;font-size:1.4rem}#page-landing .total-connections img{margin-left:0.8rem}@media (min-width: 1100px){#page-landing #page-landing-content{max-width:1100px;display:grid;grid-template-rows:350px 1fr;grid-template-columns:2fr 0.5fr 1.5fr;grid-template-areas:"logo hero hero" "buttons buttons status"}#page-landing .logo-container{grid-area:logo;margin:0;align-self:center;text-align:left;text-align:initial}#page-landing .logo-container img{height:100%}#page-landing .logo-container h2{font-size:3.6rem}#page-landing .hero-image{grid-area:hero;align-self:end}#page-landing .buttons-container{grid-area:buttons;justify-content:flex-start}#page-landing .buttons-container a{font-size:2.4rem}#page-landing .total-connections{grid-area:status;justify-content:flex-end;white-space:nowrap}}

.page-header{position:relative;display:flex;flex-direction:column;background-color:var(--color-primary);color:var(--color-text-in-primary)}.page-header .top-bar-container{display:flex;justify-content:space-between;align-items:center;width:90%;margin:0 auto;padding:1.6rem 0;color:var(--color-text-in-primary)}.page-header .top-bar-container a{height:3.2rem;transition:opacity 200ms}.page-header .top-bar-container a:hover{opacity:0.6}.page-header .top-bar-container>img{height:2rem}.page-header .header-content{width:90%;margin:0 auto}.page-header .header-content strong{display:block;margin:3.2rem 0;font:700 3.6rem Archivo;line-height:4.2rem;color:var(--color-title-in-primary)}.page-header .header-content p{max-width:30rem;margin-top:2.4rem;font-size:1.6rem;line-height:2.6rem;color:var(--color-text-in-primary)}.page-header::after{content:'';position:absolute;display:block;width:100%;height:10rem;top:100%;z-index:-1;background-color:var(--color-primary)}@media (min-width: 700px){.page-header{height:30rem}.page-header .header-content{flex-grow:1;display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.page-header .header-content strong{max-width:40rem;margin:auto 0}.page-header .header-content p{max-width:30rem;margin-top:0}}

.teacher-item{margin-top:2.4rem;background-color:var(--color-box-base);border:1px solid var(--color-line-in-white);border-radius:0.8rem}.teacher-item header{display:flex;align-items:center;margin-bottom:1.2rem;padding:2rem}.teacher-item header img{width:8rem;height:8rem;margin-right:1rem;border-radius:50%;object-fit:cover}.teacher-item header h3{font:700 2.4rem Archivo}.teacher-item header span{display:block;margin-top:1rem;font-size:1.6rem}.teacher-item>p{padding:0 2rem;margin-bottom:2rem;font-size:1.6rem;line-height:2.8rem}.teacher-item footer{display:flex;align-items:center;justify-content:center;flex-wrap:wrap;padding:3.2rem;background-color:var(--color-box-footer);border-top:1px solid var(--color-line-in-white);border-radius:0 0 0.8rem 0.8rem}.teacher-item footer strong{margin-left:0.8rem;font:700 2.2rem Archivo;color:var(--color-primary)}.teacher-item footer .buttons-container{display:flex;width:100%;margin-top:2.4rem}.teacher-item footer a,.teacher-item footer button{display:flex;align-items:center;justify-content:center;height:5.6rem;padding:0 1.4rem;border:0;border-radius:0.8rem;font:700 1.4rem Archivo;text-decoration:none;cursor:pointer;transition:background-color 200ms}.teacher-item footer a#favorite-button,.teacher-item footer button#favorite-button{width:5.6rem;margin-right:1.2rem;background-color:var(--color-primary-lighter)}.teacher-item footer a#favorite-button img,.teacher-item footer button#favorite-button img{outline:1px solid var(--color-line-in-white)}.teacher-item footer a#favorite-button:hover,.teacher-item footer button#favorite-button:hover{background-color:var(--color-primary-light)}.teacher-item footer a#contact-button,.teacher-item footer button#contact-button{flex-grow:1;background-color:var(--color-secondary);color:var(--color-button-text)}.teacher-item footer a#contact-button img,.teacher-item footer button#contact-button img{margin-right:1.4rem}.teacher-item footer a#contact-button:hover,.teacher-item footer button#contact-button:hover{background-color:var(--color-secondary-dark)}@media (min-width: 700px){.teacher-item header{padding:3.2rem}.teacher-item header img{margin-right:2rem}.teacher-item>p{padding:0 3.2rem;margin-bottom:3.2rem}.teacher-item footer{justify-content:space-between;flex-wrap:nowrap;width:auto}.teacher-item footer .buttons-container{display:block;width:auto;margin:0}.teacher-item footer button{font-size:1.6rem}.teacher-item footer button#favorite-button{display:none}.teacher-item footer button#contact-button{flex-grow:0;width:24.5rem}}

.input-block{position:relative}.input-block:focus-within::after{content:'';position:absolute;display:block;height:2px;left:1.6rem;right:1.6rem;bottom:0;background-color:var(--color-primary-light)}.input-block+.input-block,.input-block+.select-block,.input-block+.textarea-block{margin-top:1.4rem}.input-block label{display:flex;align-items:center}.input-block label small{margin-left:0.8rem;color:var(--color-text-complement);font-size:1.2rem;line-height:2rem}.input-block label small::before{content:'('}.input-block label small::after{content:')'}.input-block input{height:5.6rem;margin-top:0.8rem;padding:0 1.6rem;border-radius:0.8rem;background-color:var(--color-input-background);border:1px solid var(--color-line-in-white);outline:0;font:1.6rem Archivo;width:100%}

.select-block{position:relative}.select-block:focus-within::after{content:'';position:absolute;display:block;height:2px;left:1.6rem;right:1.6rem;bottom:0;background-color:var(--color-primary-light)}.select-block+.input-block,.select-block+.select-block,.select-block+.textarea-block{margin-top:1.4rem}.select-block label{display:flex;align-items:center}.select-block label small{margin-left:0.8rem;color:var(--color-text-complement);font-size:1.2rem;line-height:2rem}.select-block label small::before{content:'('}.select-block label small::after{content:')'}.select-block label:focus-within::after{bottom:2px}.select-block select{height:5.6rem;margin-top:0.8rem;padding:0 1.6rem;border-radius:0.8rem;background-color:var(--color-input-background);border:1px solid var(--color-line-in-white);outline:0;font:1.6rem Archivo;width:100%}

#page-teacher-list{width:100vw;height:100vh;overflow:auto}#page-teacher-list #search-teachers label{color:var(--color-text-in-primary)}#page-teacher-list #search-teachers button{width:100%;height:5.6rem;margin-top:3.2rem;border:0;border-radius:0.8rem;font:700 1.6rem Archivo;color:var(--color-button-text);background-color:var(--color-secondary);transition:background-color 200ms;cursor:pointer}#page-teacher-list #search-teachers button:hover{background-color:var(--color-secondary-dark)}#page-teacher-list main{margin:4.2rem auto;width:90%}#page-teacher-list main .no-results{display:flex;align-items:center;justify-content:center;margin-top:2.4rem;padding:6.4rem;background-color:var(--color-box-base);border:1px solid var(--color-line-in-white);border-radius:0.8rem}#page-teacher-list main .no-results p{color:var(--color-text-complement)}@media (min-width: 700px){#page-teacher-list{max-width:100%}#page-teacher-list .page-header{height:45rem}#page-teacher-list #search-teachers{display:grid;width:100%;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(2, 1fr);grid-gap:1.6rem}#page-teacher-list #search-teachers .input-block+.input-block,#page-teacher-list #search-teachers .select-block+.input-block,#page-teacher-list #search-teachers .select-block+.select-block{margin:0}}

.textarea-block{position:relative}.textarea-block:focus-within::after{content:'';position:absolute;display:block;height:2px;left:1.6rem;right:1.6rem;bottom:0;background-color:var(--color-primary-light)}.textarea-block+.input-block,.textarea-block+.select-block,.textarea-block+.textarea-block{margin-top:1.4rem}.textarea-block label{display:flex;align-items:center}.textarea-block label small{margin-left:0.8rem;color:var(--color-text-complement);font-size:1.2rem;line-height:2rem}.textarea-block label small::before{content:'('}.textarea-block label small::after{content:')'}.textarea-block label:focus-within::after{bottom:2px}.textarea-block textarea{height:5.6rem;margin-top:0.8rem;padding:0 1.6rem;border-radius:0.8rem;background-color:var(--color-input-background);border:1px solid var(--color-line-in-white);outline:0;font:1.6rem Archivo;width:100%;height:16rem;max-height:80vh;min-height:16rem;padding:1.2rem 1.6rem;resize:vertical}

#page-teacher-form{width:100vw;height:100vh;overflow:auto}#page-teacher-form main{margin:3.2rem auto;padding-top:3.2rem;border-radius:0.8rem;border:1px solid var(--color-line-in-white);background-color:var(--color-box-base)}#page-teacher-form fieldset{border:0;padding:0 3.2rem}#page-teacher-form fieldset+fieldset{margin-top:3.2rem}#page-teacher-form .legend{width:100%;display:flex;align-items:center;justify-content:space-between;margin-bottom:2.4rem;padding-bottom:1.6rem;border-bottom:1px solid var(--color-line-in-white);font:700 2.4rem Archivo;color:var(--color-text-title)}#page-teacher-form .legend button{border:0;background:none;color:var(--color-primary);transition:color 200ms;cursor:pointer}#page-teacher-form .legend button:hover{color:var(--color-primary-dark)}#page-teacher-form footer{margin-top:3.2rem;padding:3.2rem;border-top:1px solid var(--color-line-in-white);background-color:var(--color-box-footer);border-radius:0 0 0.8rem 0.8rem}#page-teacher-form footer img{margin-right:2rem}#page-teacher-form footer p{display:flex;align-items:center;font-size:1.4rem;line-height:2.4rem;color:var(--color-text-complement)}#page-teacher-form footer button{width:100%;height:5.6rem;margin-top:3.2rem;border:0;border-radius:0.8rem;font:700 1.6rem Archivo;color:var(--color-button-text);background-color:var(--color-secondary);transition:background-color 200ms;cursor:pointer}#page-teacher-form footer button:hover{background-color:var(--color-secondary-dark)}#page-teacher-form .schedule-form{display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(2, 1fr);grid-template-areas:"weekday weekday" "time-from time-to";grid-column-gap:1.6rem;-webkit-column-gap:1.6rem;column-gap:1.6rem}#page-teacher-form .schedule-form>:nth-child(1){grid-area:weekday;margin-bottom:1.4rem}#page-teacher-form .schedule-form>:nth-child(2){grid-area:time-from}#page-teacher-form .schedule-form>:nth-child(3){grid-area:time-to}#page-teacher-form .schedule-form+.schedule-form{margin-top:1.4rem}@media (min-width: 700px){#page-teacher-form{max-width:100%}#page-teacher-form main{padding-top:4rem}#page-teacher-form fieldset{padding:0 6.4rem}#page-teacher-form fieldset+fieldset{margin-top:4rem}#page-teacher-form footer{display:flex;align-items:center;justify-content:space-between;margin-top:4rem;padding:4rem 6.4rem}#page-teacher-form footer button{width:auto;margin-top:0;padding:0 3.2rem}#page-teacher-form .schedule-form{grid-template-columns:2fr 1fr 1fr;grid-template-rows:1fr;grid-template-areas:"weekday time-from time-to"}#page-teacher-form .schedule-form>:nth-child(1){margin-bottom:0}}

:root{--color-background: #f0f0f7;--color-primary-lighter: #9871f5;--color-primary-light: #916bea;--color-primary: #8257e5;--color-primary-dark: #774dd6;--color-primary-darker: #6842c2;--color-secondary: #04d361;--color-secondary-dark: #04bf58;--color-title-in-primary: #ffffff;--color-text-in-primary: #d4c2ff;--color-text-title: #32264d;--color-text-complement: #9c98a6;--color-text-base: #6a6180;--color-line-in-white: #e6e6f0;--color-input-background: #f8f8fc;--color-button-text: #ffffff;--color-box-base: #ffffff;--color-box-footer: #fafafc;font-size:60%}*{margin:0;padding:0;box-sizing:border-box}html,body,#root{height:100vh}body{background-color:#f0f0f7;background-color:var(--color-background)}#root{display:flex;align-items:center;justify-content:center}body,input,button,select,textarea{font:500 1.6rem Poppins;color:#6a6180;color:var(--color-text-base)}img{max-width:100%;height:auto}.container{width:90vw;max-width:700px}@media (min-width: 700px){:root{font-size:62.5%}}

